.publications-container {
  max-width: 900px;
  margin: 40px auto;
  padding: 20px;
}

/* page logo positioning to match Experience page */
.publications-logo-container {
  display: flex;
  justify-content: left;
  padding-top: 25px;
}

.publications-logo {
  display: flex;
  position: fixed;
  border: 1px solid white;
  border-radius: 50%;
  box-shadow: 0px 4px 10px rgba(0, 0, 0, 0.25);
  /* align with navbar top */
  top: 3vh;
  height: 40px;
  width: 40px;
  align-items: center;
  justify-content: center;
  z-index: 999;
}

.publications-title {
  font-size: 2rem;
  margin-bottom: 20px;
}

.publications-list {
  display: flex;
  flex-direction: column;
  gap: 18px;
}

.publication-item {
  border: 1px solid #e6e6e6;
  padding: 16px;
  border-radius: 6px;
  background: #fff;
}



.publication-header {
  display: flex;
  /* title on the left, year aligned right of the box */
  justify-content: space-between;
  align-items: center;
  margin-bottom: 8px;
  width: 100%;
}

.publication-title {
  color: #222; /* dark gray */
  font-weight: 600;
  text-decoration: none;
  font-size: 1rem; /* match year size */
  flex: 1 1 auto;
}

.publication-title:hover {
  /* keep interaction but do not underline or change color */
  text-decoration: none;
  color: #222;
}

.publication-year {
  color: #666;
  font-size: 1rem; /* match title */
  opacity: 0.9;
  flex: 0 0 auto;
  margin-left: 12px;
}

.publication-meta {
  color: #444;
  font-size: 0.95rem;
  margin-bottom: 8px;
}

.publication-abstract {
  color: #333;
  font-size: 0.95rem;
  line-height: 1.3;
}

.publication-actions {
  margin-top: 12px;
  display: flex;
  gap: 8px;
  flex-wrap: wrap;
}

.btn-mini {
  display: inline-block;
  padding: 6px 10px;
  font-size: 0.85rem;
  border-radius: 6px;
  text-decoration: none;
  background: var(--link-color);
  color: #fff;
  border: none;
}

.btn-mini:hover {
  /* slightly darker teal to match site theme */
  background: #0e9a8e;
}
